@import url(https://fonts.googleapis.com/css?family=Merienda&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  color: #fff;
  background-color: #202040;
  height: 100%;
  width: 100%; }

.App {
  width: 768px;
  margin: 50px auto; }

.game-actions {
  margin-bottom: 20px;
  display: flex; }
  .game-actions .start-game {
    border: 0;
    background-color: #b030b0;
    color: #fff;
    border-radius: 5px;
    font-size: 1.2rem;
    padding: 5px 15px;
    cursor: pointer;
    -webkit-transition: all 0.5s;
    transition: all 0.5s;
    margin-right: 5px; }
    .game-actions .start-game:hover {
      background-color: #602080; }

.game-bar {
  height: 70px;
  margin-bottom: 50px;
  background-color: #202060;
  padding: 1rem;
  text-align: center;
  display: flex;
  justify-content: space-around;
  align-items: center;
  font-size: 1.2rem;
  border-radius: 5px; }
  .game-bar span {
    display: block;
    font-weight: bold; }

.game {
  margin: 50px auto 0;
  background-color: #202060;
  padding: 10px;
  margin: 0 auto;
  border-radius: 10px;
  display: flex;
  flex-wrap: wrap; }
  .game .home {
    font-family: 'Merienda', cursive;
    font-weight: bold;
    text-shadow: 3px 4px 0 rgba(0, 0, 0, 0.2); }
  .game.game2 {
    width: 210px;
    height: 210px; }
    .game.game2 .home {
      width: calc(50% - 6px);
      height: calc(50% - 6px); }
  .game.game3 {
    width: 310px;
    height: 310px; }
    .game.game3 .home {
      width: calc(33.333333% - 6px);
      height: calc(33.333333% - 6px); }
  .game.game4 {
    width: 410px;
    height: 410px; }
    .game.game4 .home {
      width: calc(25% - 6px);
      height: calc(25% - 6px); }
  .game .home {
    margin: 3px;
    border-radius: 5px; }
  .game .ball {
    background-color: #b030b0;
    width: 100px;
    height: 100px;
    border-radius: 10px;
    font-size: 4rem;
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    user-select: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    position: relative;
    -webkit-transition: opacity 0.3s ease-in-out;
    transition: opacity 0.3s ease-in-out; }
    .game .ball:hover {
      opacity: 0.7; }
    .game .ball.no-ball {
      background-color: transparent;
      cursor: default;
      user-select: none;
      -webkit-user-select: none;
      -khtml-user-select: none;
      -moz-user-select: none;
      -ms-user-select: none; }
    .game .ball.correct-ball {
      background-color: #64b2cd; }
    .game .ball.disable-ball {
      pointer-events: none; }

.animated {
  -webkit-animation-duration: 0.3s;
  animation-duration: 0.3s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  position: relative;
  z-index: 999; }

@-webkit-keyframes slideInDown {
  from {
    -webkit-transform: translate3d(0, -100%, 0);
    transform: translate3d(0, -100%, 0);
    visibility: visible; }
  to {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0); } }

@keyframes slideInDown {
  from {
    -webkit-transform: translate3d(0, -100%, 0);
    transform: translate3d(0, -100%, 0);
    visibility: visible; }
  to {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0); } }

.slideInDown {
  -webkit-animation-name: slideInDown;
  animation-name: slideInDown; }

@-webkit-keyframes slideInLeft {
  from {
    -webkit-transform: translate3d(-100%, 0, 0);
    transform: translate3d(-100%, 0, 0);
    visibility: visible; }
  to {
